@import url(https://fonts.googleapis.com/css?family=Vollkorn|Roboto);
.navigation-items {
  color: white; }

.button_container {
  position: fixed;
  top: 5%;
  right: 2%;
  height: 27px;
  width: 35px;
  cursor: pointer;
  z-index: 100;
  transition: opacity .25s ease; }

.button_container:hover {
  opacity: .7; }

.white span {
  background: white !important; }

.black span {
  background: black !important; }

.button_container.active .top {
  transform: translateY(10px) translateX(0) rotate(45deg);
  background: #fff; }

.button_container.active .middle {
  opacity: 0;
  background: #fff; }

.button_container.active .bottom {
  transform: translateY(-10px) translateX(0) rotate(-45deg);
  background: #fff; }

.button_container span {
  background: #333;
  border-radius: 10px;
  border: none;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0;
  transition: all .35s ease;
  cursor: pointer; }

.button_container span:nth-of-type(2) {
  top: 10px; }

.button_container span:nth-of-type(3) {
  top: 20px; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, width .35s;
  z-index: 50; }

.overlay:before {
  content: '';
  background: #222;
  left: -55%;
  top: 0;
  width: 50%;
  height: 100%;
  position: absolute;
  transition: left .35s ease; }

.overlay:after {
  content: '';
  background: #222;
  right: -55%;
  top: 0;
  width: 53%;
  height: 100%;
  position: absolute;
  transition: all .35s ease; }

.overlay.open {
  opacity: .9;
  visibility: visible;
  height: 100%; }

.overlay.open:before {
  left: 0; }

.overlay.open:after {
  right: 0; }

.overlay.open li:nth-child(even) {
  -webkit-animation-delay: .45s;
  animation-delay: .45s;
  -webkit-animation: fadeInRight .5s ease forwards;
  animation: fadeInRight .5s ease forwards;
  -webkit-animation-delay: .35s;
  animation-delay: .35s; }

.overlay.open li:nth-child(odd) {
  -webkit-animation-delay: .45s;
  animation-delay: .45s;
  -webkit-animation: fadeInLeft .4s ease forwards;
  animation: fadeInLeft .4s ease forwards;
  -webkit-animation-delay: .35s;
  animation-delay: .35s; }

.overlay nav {
  position: relative;
  height: 50%;
  top: 35%;
  transform: translateY(-50%);
  font-size: 50px;
  font-weight: 500;
  text-align: center;
  z-index: 100; }

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%; }

.overlay ul li {
  display: block;
  height: 25%;
  height: calc(100% / 6);
  min-height: 50px;
  position: relative;
  opacity: 0; }

.overlay ul li a {
  display: block;
  position: relative;
  color: #fff;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  font-size: 30px; }

.overlay ul li a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #fff;
  transition: .35s; }

@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

