$blue: #548cd1;
$teal: #33b7bf;
$purple: #5b54d1;
$red: #ff0000;

* {
	margin: 0 auto;
	box-sizing: border-box;
	font-weight: 100;
	font-family: sans-serif;
}


.fees-terms{
	p{
		font-size: 20px!important;
	}
	
}

#root{
	// background-color: $red!important;
}

html { 

   scroll-behavior: smooth;
}

ul { list-style-type: none;  }
li{font-size: 27px!important}
a:link {text-decoration: none;}

.cont{
	padding: 0px;
	width: 90%; 
	margin: 0 auto;
}

.title-div{
	order: 1;
}

section{
	height: auto;
	margin-bottom: 30vh;
	text-align: center;
	h1{ text-align: right; font-size: 10em; line-height: 112px; padding-top: 50px }
	h2{ font-size: 4em; margin: 5px }
	p{ font-size: 27px }
}

p{font-size: 27px!important}

.contactus{
	margin-bottom: 0vh;

}

.redbackground{
	background-color: $red
}


.apply-before{
	text-align: left;
	font-size: 3em;
	line-height: 60px;
	color: red;
	span{
		font-weight: 300;
		background-color: yellow;	
	}
	
}

.flex-space-between{
	display: flex;
	justify-content: space-between
}

.title-left{
		font-weight: bold;
		color: red;
		padding-top: 10px;
		text-align: left;
		font-size: 5em; line-height: 112px; 

}

.title{
	font-weight: bold;
	padding-top: 60px;
}
.main-title{
	padding-top: 60px;	

}

.home{
	h1{
		font-weight: bold
	}
}

.howtoapply{

	li{font-size: 18px}
}

.margin-left-15{
	margin-left: 15px;
}

.margin-left-15{
	margin-right: 15px;
}

.margin-top-150{
	margin-top: 150px
}

.margin-top-300{
	margin-top: 300px;
}

.guests{
	margin: 0px;
	h2{
		padding-bottom: 30px;
		padding-top: 40px;
	}
	p{
		font-size: 20px;
		text-align:center;
	}

}

.justify-no{
	justify-content: unset !important;
}
.text-left{
	text-align: left;
}
.text-right{
	text-align: right;
}

.arrow-down{
	margin-top: 5vh;
	animation: 5s infinite;
	a { color: white }
}

.flex{
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
	


	div{
  display: flex; 
  flex-basis: calc(45% - 14px);  
  
  flex-direction: column;

  	p{

  		font-size: 17px;

  	}
	}
}


/* Mobile portrait */
@media screen and (max-width: 870px) {
	section{ 
		h1{ font-size: 7rem; line-height: 70px; }
		h2{ font-size: 4em; margin: 0px }
		p{ font-size: 27px;margin: 0px; }

		.overlay ul li a { font-size: 10px; }
	}

		.flex{
		  min-height: 100%;
		  display: block;
			width: 100%;
			div{ margin-top: 20px;
		  	p{ font-size: 17px;} 
			}
		}


		.wrp{display: block; width: 100%!important;}

 }
/* Mobile portrait */
@media screen and (max-width: 800px) {
	section{ 
		h1{ font-size: 7rem; line-height: 70px; }
		h2{ font-size: 4em; margin: 0px }
		p{ font-size: 27px;margin: 0px; }
		.overlay ul li a { font-size: 10px; }
	}
.wrp{display: block; width: 100%!important;}
 
	.mobile-block{
		display: block;
		text-align: center;
		// font-size: 5em; line-height: 112px; 
	}

 }	

 
/* Mobile portrait */
@media screen and (max-width: 500px) {
	section{ 
		h1{ font-size: 5rem; line-height: 50px; }
		h2{ font-size: 2em; margin: 0px }
		p{ font-size: 17px;margin: 0px; }
		.overlay ul li a { font-size: 10px; }
	}

.wrp{display: block; width: 100%!important;}


 }




/* Mobile portrait */
@media screen and (max-width: 400) {
	section{ 
		h1{ font-size: 3rem; line-height: 50px; }
		h2{ font-size: 1em; margin: 0px }
		p{ font-size: 17px;margin: 0px; }
		.overlay ul li a { font-size: 10px; }


	}
.wrp{display: block; width: 100%!important;}

 }





.wrp{
	width:50%;
	display: inline-block;
	vertical-align: top
}


 .week-p{
		font-size: 21px;
		text-align: left;
 }

